@import url('https://fonts.googleapis.com/css?family=PT+Sans:400,700');
 html {
	 font-size: 10px;
	 -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	 box-sizing: border-box;
}
 *, *:before, *:after {
	 margin: 0;
	 padding: 0;
	 box-sizing: inherit;
}
 body {
	 font-family: 'PT Sans', sans-serif;
	 font-size: 16px;
	 line-height: 1.428571429;
	 font-weight: 400;
	 color: #161515;
	 /* color: #fff; */
}
 .row {
	 display: flex;
	 align-items: center;
	 justify-content: center;
}
 .section {
	 background-color: #be8556;
	 position: relative;
	 overflow: hidden;
	 display: flex;
	 justify-content: center;
	 flex-direction: column;
}
 header, main, footer {
	 display: block;
	 position: relative;
	 z-index: 1;
}
 header {
	 padding: 48px;
}
 @media (max-width: 440px) {
	 header {
		 padding: 48px 24px;
	}
}
 header > h3 {
	 font-size: 44px;
	 font-weight: 700;
	 margin-bottom: 8px;
	color: rgb(183, 195, 233);

}
 header > h4 {
	 font-size: 22px;
	 font-weight: 400;
	 letter-spacing: 1px;
	color: rgb(183, 195, 233);

}

header > h5 {
	font-size: 16px;
	/* font-weight: 300; */
	font-weight: 520;
	color: rgb(183, 195, 233);
}

 main {
	 flex: 1;
	 padding: 0 48px;
}
 @media (max-width: 440px) {
	 main {
		 padding: 0 24px;
	}
}
 footer {
	 width: 100%;
	 background-color: #524f81;
	 padding: 16px;
	 align-self: center;
	 text-align: center;
	 margin-top: 32px;
}
 footer a {
	 color: #fff;
	 font-weight: 700;
	 text-decoration: none;
}
 footer a:hover {
	 text-decoration: underline;
}
 form {
	 height: 100%;
	 display: flex;
	 justify-content: center;
	 flex-direction: column;
}
 .label {
	color: #161515;
	 /* color: rgba(226, 227, 232, .75); */
	 font-size: 16px;
}
 small {
	 display: none;
}
 small.errorOnce {
	 margin-top: 2px;
}
 .form-item input[type="text"], .form-item input[type="number"], .form-item input[type="password"], .form-item input[type="email"] {
	 display: block;
	 color: #161515;
	 /* color: #e2e3e8; */
	 font-size: 16px;
	 width: 100%;
	 background-color: transparent;
	 border: none;
	 border-bottom: 1px solid #75759e;
	 padding: 8px 0;
	 appearance: none;
	 outline: none;
}
 .form-item small {
	/*letter-spacing: 1px;
	*/
}
 .form-item i {
	 font-size: 12px;
	 color: red;
}
 .box-item {
	 height: 60px;
	 margin-bottom: 16px;
}
 .form-item-double {
	 display: flex;
}
 .form-item-double .form-item {
	 flex: 1 1 auto;
}
 .form-item-double .form-item:nth-child(1) {
	 padding-right: 16px;
}
 .form-item-double .form-item:nth-child(2) {
	 padding-left: 16px;
}
 .form-item-triple {
	 display: flex;
	 align-items: center;
	 padding-top: 6px;
}
 .form-item-triple .radio-label {
	 flex: 1 1 auto;
	 text-align: left;
}
 .form-item-triple .radio-label label {
	 display: inline-block;
	 vertical-align: middle;
}
 .form-item-triple .form-item {
	 flex: 3 1 auto;
	 text-align: center;
	 margin: 0;
}
 .form-item-triple .form-item label, .form-item-triple .form-item input[type="radio"] {
	 display: inline-block;
	 vertical-align: middle;
	 margin: 0 4px;
}
 ::-webkit-input-placeholder {
	/* WebKit, Blink, Edge */
	 color: rgba(226, 227, 232, .75);
	 font-size: 16px;
}
 :-moz-placeholder {
	/* Mozilla Firefox 4 to 18 */
	 color: rgba(226, 227, 232, .75);
	 font-size: 16px;
	 opacity: 1;
}
 ::-moz-placeholder {
	/* Mozilla Firefox 19+ */
	 color: rgba(226, 227, 232, .75);
	 font-size: 16px;
	 opacity: 1;
}
 :-ms-input-placeholder {
	/* Internet Explorer 10-11 */
	 color: rgba(226, 227, 232, .75);
	 font-size: 16px;
}
 ::-ms-input-placeholder {
	/* Microsoft Edge */
	 color: rgba(226, 227, 232, .75);
	 font-size: 16px;
}
 .submit {
	 display: inline-block;
	 font-size: 18px;
	 font-weight: 700;
	 letter-spacing: 1px;
	 padding: 8px 48px;
	 margin-top: 32px;
	 border: 2px solid #75759e;
	 border-radius: 20px;
	 cursor: pointer;
	 transition: all ease 0.2s;
}
 .submit:hover {
	 background-color: #eda261;
	 border: 2px solid #eda261;
}
 .wave {
	 position: absolute;
	 top: 0;
	 left: 50%;
	 width: 800px;
	 height: 800px;
	 margin-top: -600px;
	 margin-left: -400px;
	 background: #453d25;
	 border-radius: 40%;
	 animation: shift 20s infinite linear;
	 z-index: 0;
}
 @keyframes shift {
	 from {
		 transform: rotate(360deg);
	}
}

.buttons__div{
	display: flex;
	justify-content: center;
	gap: 10rem;
}


/* nav */
.topnav {
	background-color: #a15a18;
	overflow: hidden;
  }
  
  /* Style the links inside the navigation bar */
  .topnav a {
	float: left;
	color: #f2f2f2;
	text-align: center;
	padding: 14px 16px;
	text-decoration: none;
	font-size: 17px;
  }
  
  /* Change the color of links on hover */
  .topnav a:hover {
	background-color: #ddd;
	color: black;
  }
  
  /* Add a color to the active/current link */
  .topnav a.active {
	background-color: #170744;
	color: white;
	font-weight: 600;
  }